@import url("https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@400;700&display=swap");

html,
body {
  margin: 40px auto;
  padding: 0;
  max-width: 1100px;
  font-family: "Roboto Condensed", sans-serif;
}
.App {
  position: relative;
}

p,
h1,
h2,
h3,
h4,
h5,
h6,
li,
ul,
ol,
dl,
span {
  margin: 0;
  padding: 0;
  font-size: unset;
}

.intro {
  margin-top: 50px;
  height: 250px;
  width: 1100px;
  display: flex;
  flex-wrap: wrap;
  box-shadow: 1px 1px 15px rgba(0, 0, 0, 0.3);
  border-radius: 4px;
  overflow: hidden;
}
.intro-item {
  position: relative;
  box-sizing: border-box;
  width: 550px;
  padding: 40px 35px;
  display: flex;
  justify-content: space-between;
  gap: 30px;

  height: 250px;
}
.btn {
  background-color: #9f0013;
}
.btn,
.btn a {
  text-decoration: none;
  cursor: pointer;
  clip-path: polygon(0 15%, 15% 0, 100% 0, 100% 85%, 85% 100%, 0 100%);
  border: none;
  width: 101px;
  height: 38px !important;

  color: white;
  padding: 0;
  font-size: 14px;
  text-transform: uppercase;
  font-family: "Roboto Condensed", sans-serif;
}
.btn a {
  text-align: center;
  /* margin-top: 20px; */
}
.btn-active {
  background-color: #5c5c5c;
  text-align: center;
}

.content {
  max-width: 1100px;
  /* height: 1200px; */
  margin-top: 50px;
  display: flex;
  justify-content: space-between;
  /* background-color: rgb(255, 222, 222); */
}

.details__search {
  display: flex;
  flex-direction: column;
  align-items: end;
}
.hero-details {
  box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.3);
  box-sizing: border-box;
  width: 425px;
  /* background-color: rgb(173, 173, 173); */
  padding: 25px;
  display: flex;
  flex-direction: column;
  margin: 0;
  border-radius: 4px;
}
.hero-details img {
  height: 150px;
  width: 150px;
}
.hero-details-head {
  display: flex;
  text-transform: uppercase;
  height: 150px;
}
.hero-details-head-name {
  margin-left: 25px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.hero-details-head-name-buttons {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.hero-details-bio {
  margin-top: 15px;
  font-size: 14px;
}
.hero-details-comics {
  margin-top: 15px;
  display: flex;
  flex-direction: column;
}

.strange {
  z-index: 2;
  width: 467px;
  height: 372px;
  position: absolute;
  bottom: 0;
  right: -150px;
}

.search {
  border-radius: 4px;
  box-sizing: border-box;
  z-index: 1;
  margin-top: 50px;
  width: 425px;
  height: 127px;
  background-color: #fff;
  padding: 25px;
  display: flex;
  flex-direction: column;
  box-shadow: 2px 1px 5px rgba(0, 0, 0, 0.3);
}
.search:hover {
  z-index: 3;
}
.search-content {
  display: flex;
  flex-direction: column;
  gap: 18px;
  width: 300px;
  justify-content: space-between;
}
.search-content h4 {
  font-size: 18px;
}
.searcher {
  display: flex;
  justify-content: space-between;
}
.error-message {
  display: flex;
  width: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.error-wrapper {
  margin-top: 50px;
  display: flex;
  width: 80vw;
}
.error-number {
  font-size: 20vh;
  font-weight: 700;
}
.error-desc-wrapper {
  margin-left: 5vh;
  display: flex;
  flex-direction: column;
  gap: 2vh;
  justify-content: center;
  align-items: center;
}
.error-desc-text {
  width: 50vw;
  font-weight: 700;
  font-size: 3vh;
}
.single-comics {
  margin-top: 50px;
  display: flex;
  gap: 10vw;
}
.single-comics img {
  height: 60vh;
}
.single-comics-title {
  font-weight: 700;
  font-size: 3vh;
}
.single-comics-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 2.5vh;
}
.single-comics-price {
  font-weight: 700;
  font-size: 2.5vh;
}
.single-comics-wrapper button {
  justify-self: end;
}
.single-comics-creators {
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 0.3vh;
}
.single-comics-creators li:nth-child(1) {
  margin-top: 1vh;
}
